









































import {RadioGroup,RadioButton, MenuItem,MenuItemGroup,Submenu,Menu,Button} from 'element-ui'
export default {
    data () {
        return {
            isCollapse: true,
            _this: this,
            menus: []
        }

    },
    components:{
        [RadioGroup.name]:RadioGroup,
        [RadioButton.name]:RadioButton,
        [Menu.name]:Menu,
        [MenuItemGroup.name]:MenuItemGroup,
        [Submenu.name]:Submenu,
        [Button.name]:Button,
        [MenuItem.name]:MenuItem,
    },
    created () {
      this.menuData();
    },
    methods:{
        menuData:function () {
            let _this = this;
            this.$ajax.get(this.$urls.menu).then(function (ret) {
                _this.menus = ret.data
            })
        }
    },
}
