































export default {
    name: 'top',
    data () {
        return {
            isShow: true,
            optShow: false,
            user: ''
        }
    },
    created(){
      this.loadUser();
    },
    methods: {
        loadUser: function () {
            try{
                this.user = JSON.parse(this.$cookies.get("user"));
            } catch (e) {
                this.isShow = false;
                this.user = {
                    nickname: '未登录',
                    avatar: '/default.jpg'
                }
            }
        },
        logout: function () {
            this.$cookies.clear();
            this.loadUser();
        }
    }
}
